import _app from '../../js_sdk/QuShe-SharerPoster/util/QS-SharePoster/app.js';
import {
	getSharePoster
} from '../../js_sdk/QuShe-SharerPoster/util/QS-SharePoster/QS-SharePoster.js';
import {uniPopup} from '../../components/uni/uni-popup/uni-popup.vue'
import './index.scss'

export default {
	components: {uniPopup},
	data() {
		return {
			imgSrc: this.$store.state.imgSrc,
			swiper: [{
				img: '../../static/productInfo/banner1.png'
			}, {
				img: '../../static/productInfo/banner2.png'
			}],
			current: 0,
			tab: 1,//商品详情1，用户评价2
			commentList: [
				{
					avatar: '../../static/index/add.png',
					name: '张先生',
					star: 2,
					date: '2019-12-25',
					content: '机油收到了，已经购买了几次了，值得信赖的商家，还是一如既往的好，和实体店购买的一样，实惠质量也非常不错！！！！',
					commentPic: ['../../static/index/listpic1.png','../../static/index/listpic1.png','../../static/index/listpic1.png']
				},
				{
					avatar: '../../static/index/add.png',
					name: 'x先生',
					star: 3,
					date: '2019-11-25',
					content: 'xxxxxxxx！！！',
					commentPic: []
				},
			],
			/**
			 * 海报绘制相关变量
			 */
			poster: {},
			qrShow: false,
			canvasId: 'default_PosterCanvasId',
			//
		}
	},
	onLoad() {
	},
	methods: {
		swiperChange (e) {
			this.current=e.detail.current
		},
		//改变tab值
		changeTab(num){
			this.tab=num
		},
		//物流说明模态框
		LogisticsInfoModel(bool){
			bool ? this.$refs.popup.open() : this.$refs.popup.close()
		},
		gotoIndex(){
			uni.reLaunch({
				url: '/pages/index/index'
			})
		},
		/**
		 * 绘制海报
		 */
		async shareFc() {
				try {
					console.log('准备生成:' + new Date())
					const d = await getSharePoster({
						_this: this, //若在组件中使用 必传
						type: 'testShareType',
						formData: {
							//访问接口获取背景图携带自定义数据
		
						},
						posterCanvasId: this.canvasId,	//canvasId
						delayTimeScale: 20, //延时系数
						background: {
							// width: 1080,
							// height: 1920,
							width: 1000,
							height: 1600,
							backgroundColor: 'white'
						},
						drawArray: ({
							bgObj,
							type,
							bgScale
						}) => {
							const dx = bgObj.width * 0.3;
							const fontSize = bgObj.width * 0.045;
							const lineHeight = bgObj.height * 0.04;
							//可直接return数组，也可以return一个promise对象, 但最终resolve一个数组, 这样就可以方便实现后台可控绘制海报
							return new Promise((rs, rj) => {
								rs([
									{
										type: 'image',
										url: '/static/productInfo/banner1.png',
										alpha: 1,
										dx: 30,
										dy: 30,
										infoCallBack(imageInfo) {
											let scale = bgObj.width * 0.2 / imageInfo.height;
											return {
												dWidth: 690, // 因为设置了圆形图片 所以要乘以2
												dHeight: 794,
											}
										}
									},
									{
										type: 'image',
										url: '/static/productInfo/banner2.png',
										alpha: 1,
										dx: 30,
										dy: 1020,
										infoCallBack(imageInfo) {
											let scale = bgObj.width * 0.2 / imageInfo.height;
											return {
												dWidth: 160, // 因为设置了圆形图片 所以要乘以2
												dHeight: 160,
											}
										}
									},
									{
										type: 'text',
										fontStyle: 'normal',
										text: 'YBM/意奔玛空调滤清YMB3140007空调滤芯空调滤芯',
										size: 30,
										color: 'black',
										alpha: 1,
										textAlign: 'left',
										textBaseline: 'middle',
										fontFamily: 'PingFang-SC-Medium',
										infoCallBack(textLength) {
											_app.log('index页面的text的infocallback ，textlength:' + textLength);
											return {
												dx: 30,
												dy: 894,
											}
										},
										lineFeed: { //换行设置
											maxWidth: 411,
											lineHeight: 40,
											lineNum: -1,
											dx: -1
										},
										serialNum: 0,
										id: 'tag1'	//自定义标识
									},
									{
										type: 'text',
										fontStyle: 'normal',
										text: '￥366',
										size: 36,
										color: '#ff9000',
										alpha: 1,
										textAlign: 'left',
										textBaseline: 'middle',
										fontFamily: 'PingFang-SC-Medium',
										infoCallBack(textLength) {
											_app.log('index页面的text的infocallback ，textlength:' + textLength);
											return {
												dx: 611,
												dy: 920,
											}
										},
										serialNum: 0,
										id: 'tag2'	//自定义标识
									},
									{
										type: 'text',
										fontStyle: 'normal',
										text: '小将军',
										size: 28,
										color: '#333333',
										alpha: 1,
										textAlign: 'left',
										textBaseline: 'middle',
										fontFamily: 'PingFang-SC-Medium',
										infoCallBack(textLength) {
											return {
												dx: 210,
												dy: 1080,
											}
										},
										serialNum: 0,
										id: 'tag3'	//自定义标识
									},
									{
										type: 'text',
										fontStyle: 'normal',
										text: '长按识别图中二维码',
										size: 28,
										color: '#333333',
										alpha: 1,
										textAlign: 'left',
										textBaseline: 'middle',
										fontFamily: 'PingFang-SC-Medium',
										infoCallBack(textLength) {
											return {
												dx: 210,
												dy: 1140,
											}
										},
										serialNum: 0,
										id: 'tag4'	//自定义标识
									},
								]);
							})
						},
						setCanvasWH: ({
							bgObj,
							type,
							bgScale
						}) => { // 为动态设置画布宽高的方法，
							this.poster = bgObj;
						}
					});
					console.log('海报生成成功, 时间:' + new Date() + '， 临时路径: ' + d.poster.tempFilePath)
					this.poster.finalPath = d.poster.tempFilePath;
					this.qrShow = true;
				} catch (e) {
					_app.hideLoading();
					_app.showToast(JSON.stringify(e));
					console.log(JSON.stringify(e));
				}
			},
			saveImage() {

				uni.saveImageToPhotosAlbum({
					filePath: this.poster.finalPath,
					success(res) {
						_app.showToast('保存成功');
					}
				})




			},
			share() {



				

				_app.showToast('分享了');

			},
			hideQr() {
				this.qrShow = false;
			}
			
	}
}
